.nav-list {
    display: flex;
    list-style: none;
    gap: 30px;

    @media screen and (max-width:728px) {
        flex-direction: column;
        align-items: center;
        gap: 10px;
        flex-wrap: wrap;
    }
}

.link {
    text-decoration: none;
    color: white;
}

.link::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: white;
    transition: width .3s;
}

.link:hover::after {
    width: 100%;
}

@keyframes grow {
    0% {
        width: 0;
    }
    50% {
        width: 50%;
    }
    100% {
        width: 100%;
    }
  }